import { Component } from '@angular/core';
import {AuthService} from '../services/auth.service';
import {ToastrService} from 'ngx-toastr';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.scss']
})

export class NavbarComponent {
    currentLang = 'en';
    isIn = true;   // store state
    toggleClass = 'ft-maximize';
    activeTab: string;
    constructor(public authService: AuthService, private toast: ToastrService) {
        // const browserLang: string = translate.getBrowserLang();
        // translate.use(browserLang.match(/en|es|pt|de/) ? browserLang : 'en');
    }

    ChangeLanguage(language: string) {
        // this.translate.use(language);
    }

    // ToggleClass() {
    //     if (this.toggleClass === 'ft-maximize') {
    //         this.toggleClass = 'ft-minimize';
    //     } else
    //         this.toggleClass = 'ft-maximize'
    // }

  toggleState() { // click handler
    const bool = this.isIn;
    this.isIn = bool === false ? true : false;
  }

  logout(){
    this.onSubmit();
  }

  onSubmit() {
    // this.authService.logout();
    const user = JSON.parse(localStorage.getItem('user'));
    const token = JSON.parse(localStorage.getItem('token'));
    const body = {user_id: user.id, access_token: token};
    this.authService.logoutUser(body).subscribe((data) => {
      this.authService.logout();
    }, error => {
      this.toast.error(error.error.error_description);
    });

  }

  setActive(val: string){

      this.activeTab = val;
  }
}
